/* Header.css */

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    background-color: var(--primary-color); /* Change to primary color */
    color: var(--text-light);
    box-shadow: 0px 4px 12px var(--shadow-color);
    transition: background-color 0.3s ease; /* Smooth transition */
  }
  
  .header:hover {
    background-color: var(--accent-color); /* Change bg on hover */
  }
  
  .header .logo {
    font-size: 2rem; /* Increased logo size */
    color: var(--accent-color);
    font-weight: bold;
    letter-spacing: 1px; /* Spacing for a modern look */
    transition: transform 0.3s ease; /* Animation */
  }
  
  .header .logo:hover {
    transform: scale(1.05); /* Slightly increase size on hover */
  }
  
  .nav-links {
    display: flex;
    list-style: none;
    gap: 2rem;
  }
  
  .nav-links a {
    color: var(--text-light);
    text-decoration: none;
    font-weight: bold;
    font-size: 1.1rem; /* Increased font size */
    transition: color 0.3s ease, transform 0.3s ease; /* Smooth transitions */
  }
  
  .nav-links a:hover {
    color: var(--accent-color);
    transform: translateY(-3px); /* Move links up slightly on hover */
  }
  
  @media (max-width: 768px) {
    .header {
      flex-direction: column; /* Stack elements on small screens */
      align-items: flex-start; /* Align items to the start */
    }
    
    .nav-links {
      flex-direction: column; /* Stack links vertically */
      gap: 1rem;
      padding: 1rem 0; /* Padding around links */
    }
  }
  