/* Skills.css */
.skills {
    background-color: var(--light-bg); /* Light background */
    padding: 2rem;
    border-radius: var(--border-radius);
    box-shadow: 0 6px 16px var(--shadow-color);
    max-width: 800px;
    margin: 2rem auto;
    text-align: center;
  }
  
  .skills h2 {
    font-size: 2.5rem;
    color: var(--primary-color); /* Primary color for headings */
  }
  
  .skills-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 1rem;
    margin-top: 1rem;
  }
  
  .skill {
    background-color: var(--dark-bg); /* Dark background for skills */
    color: var(--text-light);
    padding: 1rem;
    border-radius: var(--border-radius);
    min-width: 120px;
    text-align: center;
    font-weight: bold;
    transition: transform 0.3s ease;
    box-shadow: 0 4px 10px var(--shadow-color);
  }
  
  .skill:hover {
    transform: scale(1.05);
    color: var(--accent-color);
  }
  