/* About.css */
.about {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--dark-bg); /* Dark background */
    color: var(--text-light);
    padding: 2rem;
    border-radius: var(--border-radius);
    box-shadow: 0 6px 16px var(--shadow-color);
    max-width: 800px;
    margin: 2rem auto;
    text-align: center;
  }
  
  .about h2 {
    font-size: 2.5rem;
    color: var(--accent-color); /* Accent color for headings */
    margin-bottom: 1rem;
  }
  
  .profile-image {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    border: 5px solid var(--primary-color); /* Primary color for border */
    margin: 1rem 0;
  }
  
  .about-text {
    font-size: 1.2rem;
    max-width: 600px;
    color: var(--text-dark); /* Dark text for better readability */
  }
  