/* index.css */
:root {
  --dark-bg: #30343F;
  --light-bg: #FAFAFF;
  --accent-color: #E4D9FF;
  --primary-color: #273469;
  --secondary-color: #1E2749;
  --text-dark: #273469; /* Dark text for contrast */
  --text-light: #FAFAFF; /* Light text */
  --shadow-color: rgba(0, 0, 0, 0.15);
  --border-radius: 10px;
  --font-main: 'Roboto', sans-serif;
}

body {
  font-family: var(--font-main);
  background-color: var(--light-bg);
  color: var(--text-dark);
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
